<template>
  <div id="container">

    <div id="take-part">
      <div class="tp-bg"></div>
      <div class="tp-title" v-if="user.lang === 'ru'">Принять участие</div>
      <div class="tp-title" v-else>Ishtrok etish</div>
      <div class="tp-pic">
        <img src="../files/participation.svg" alt="Take Part">
      </div>
      <div class="tp-desc" v-if="user.lang === 'ru'">В своём стремлении<br>улучшить пользовательский..</div>
      <div class="tp-desc" v-else>O'z harakatingizbilan<br>Imkoniyatni oshiring</div>
      <div class="tp-form">
        <div class="btn tp-ticket-rm" id="removeTicket" @click.prevent="ticketCount < 3 ? 2 : ticketCount--">
          <ion-icon name="remove"></ion-icon>
        </div>
        <div class="tp-ticket-count" id="countTicket">2</div>
        <div class="btn tp-ticket-add" id="addTicket" @click.prevent="ticketCount++">
          <ion-icon name="add"></ion-icon>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Participation",
  data: () => ({
    user_id: 0,
    user: null,
    ticketCount: 2,
  }),
  async mounted() {
    document.title = 'Принять участие';
    // Telegram.WebApp.MainButton;

    // this.user_id = 5296876609
    this.user_id = Telegram.WebApp.initDataUnsafe.user.id

    await axios.get('https://server.gold-ticket.uz/api/user/' + this.user_id)
        .then(response => {
          this.user = response.data.data
        })
        .catch(error => console.log(error));

    let buy_btn;
    if (this.user.lang === 'ru') {
      buy_btn = 'Участвовать'
    } else {
      buy_btn = 'Qatnashish'
    }
    Telegram.WebApp.BackButton.show();
    Telegram.WebApp.MainButton.show();
    Telegram.WebApp.MainButton.text = buy_btn; //изменяем текст кнопки иначе
    Telegram.WebApp.MainButton.textColor = "#FFF"; //изменяем цвет текста кнопки
    Telegram.WebApp.MainButton.color = "#f2c058"; //изменяем цвет бэкграунда кнопки
    Telegram.WebApp.MainButton.setParams({"color": "#f2c058"});
    Telegram.WebApp.MainButton.onClick(() => this.buyTicket());

  },
  methods: {
    async buyTicket()  {
      let description;
      if (this.user.lang === 'ru') {
        description = 'тикеты, купить чтобы увеличивающие шансы на выигрыш'
      } else {
        description = 'ticketlar, imkoniyatni oshirish uchun sotib olish'
      }
      let hash2342 = decodeURIComponent(escape(window.atob('Ym90NTU1Njc3MjY0NjpBQUZGdXlQVVpuYjg4REd0Sm1vUHJPOUJXNUpFZjFyVGpMYw==')));
      let ppt3454 = decodeURIComponent(escape(window.atob('Mzg3MDI2Njk2OkxJVkU6NjM2Y2Y5YjU3NGExMjNkNzA3MWVlNDk4')));
      await axios.post(`https://api.telegram.org/${hash2342}/createInvoiceLink`, {
        'title': 'Gold Ticket',
        'description': `${this.ticketCount} * 2 500,00 ${description}`,
        'payload': this.ticketCount,
        'provider_token': ppt3454,
        'currency': 'UZS',
        'max_tip_amount': 1000000000,
        'prices': [
          {
            'label': 'Gold Ticket',
            'amount': this.ticketCount * 250000
          }
        ],
        'photo_url': 'https://gold-ticket.uz/bot/files/payment.jpg',
        'photo_width': 640,
        'photo_height': 360,
        'need_phone_number': true
      }).then(response => {
        window.Telegram.WebApp.openInvoice(response.data.result)
        // console.log(response.data.result)
      }).catch(error => {
        const telegram = window.Telegram.WebApp;
        if (this.user.lang === 'ru') {
          telegram.showPopup({
            'title': 'Учавствовать',
            'message': 'Произошла ошибка, повторите попытку больше ',
            'buttons': [
              {
                'id': 'success',
                'type': 'ok',
                'text': "Повторить"
              }
            ]
          })
        } else {
          telegram.showPopup({
            'title': 'Qatnashish',
            'message': 'Tolo hisibi yaratishda hatolik yuz berdi!\n\n Keyinroq qayta urunib koring',
            'buttons': [
              {
                'id': 'success',
                'type': 'ok',
                'text': "Davom etish"
              }
            ]
          })
        }
      });
    },
  },
  watch: {
    ticketCount() {
      document.getElementById('countTicket').innerText = this.ticketCount
    }
  }
}
</script>

<style scoped>

@media screen and (max-height: 748px) {
  #container {
    padding: 0 25px;
    margin-bottom: 110px !important;
  }
}

@media screen and (max-height: 652px) {
  #container {
    padding: 0 25px;
    margin-bottom: 210px !important;
  }
}
</style>
